import React, {useState, useEffect} from "react";
import styles from '../App.module.css'
import {Carousel, Input, Flex, Button, ConfigProvider, Alert, message} from 'antd';

export function FunctionComponents(props) {
    const [name, setName] = useState('Function Components')

    const [date, setDate] = useState(new Date().toLocaleTimeString())

    const [imageList, setImageList] = useState([
        {
            name: require('../static/image/1.jpg')
        },
        {
            name: require('../static/image/2.jpg')
        },
        {
            name: require('../static/image/3.jpg')
        },
        {
            name: require('../static/image/4.jpg')
        }
    ])

    const [value, setValue] = useState('')

    const [flag, setFlag] = useState(false)

    const [alertFlag, setAlertFlag] = useState(false)

    const [messageApi, contextHolder] = message.useMessage();

    const contentStyle: React.CSSProperties = {
        width: '20rem',
        height: '9.6rem',
        color: '#fff',
        textAlign: 'center',
        background: '#EEEEEE',
        margin: '0 auto',
        borderRadius: '0.1rem'
    };

    useEffect(() => {
        const interval = setInterval(() => {
            setDate(new Date().toLocaleTimeString())
        }, 100)
        return () => {
            clearInterval(interval)
        }
    }, [])

    function imageReturn(src) {
        return src.name
    }

    function clickToView() {
        if (value === 'Xiaomei5997317') {
            setFlag(true)
        } else {
            info(true)
            setFlag(false)
        }
    }

    function setInputValue(e) {
        setValue(e.target.value)
    }

    const info = () => {
        messageApi.info('');
    };

    return (
        <div className={styles.functionComponents}>
            <ConfigProvider theme={{
                components: {
                    Message: {
                        contentBg: 'rgba(82,126,238,0.89)',
                        colorText:'#FFFFFF',
                        contentPadding:'18px 24px'
                    }
                },
                token: {
                    fontSize:24,
                    fontWeight:700
                }
            }}>
                {contextHolder}
            </ConfigProvider>
            <div style={{margin: '0 auto', width: '20rem'}}>{name}</div>
            <div className={styles.functionComponents_space}></div>
            <div style={{
                margin: '0 auto',
                width: '20rem'
            }}>{Number(date.split(':')[0]) > 12 ? '' : Number(date.split(':')[0]) === 12 ? '' : ''} {date}</div>
            <Flex vertical={false} gap={20} className={styles.functionComponents_flex}>
                <ConfigProvider theme={{
                    components: {
                        Input: {
                            paddingBlock: 10,
                            inputFontSize: 24
                        },
                        Button: {
                            contentFontSize: 24,
                            fontWeight: 700
                        },

                    },
                    token: {
                        controlHeight: 60
                    }
                }}>
                    <Input placeholder="" value={value} onChange={setInputValue} onPressEnter={clickToView}/>
                    <Button type="primary" onClick={clickToView}></Button>
                </ConfigProvider>
            </Flex>
            {
                flag ? <Carousel autoplay style={contentStyle} autoplaySpeed={2000} speed={1500}>
                    {
                        imageList.map((item, index) => {
                            return (
                                <div key={index} className={styles.functionComponents_imageList}>
                                    <img className={styles.functionComponents_img} src={imageReturn(item)} alt=""/>
                                </div>
                            )
                        })
                    }
                </Carousel> : ''
            }
        </div>
    )
}